
import './Login.css';
import React from "react";
import { useAuth0 } from "@auth0/auth0-react";

const Login = () => {
  const { loginWithRedirect } = useAuth0();

  return  <button id="loginhomebtn" onClick={() => loginWithRedirect()}>Enter<br></br> the<br></br> shop!</button>;
 
}


export default Login;